.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.8125rem 1.75rem;
  border-width: 1px;
  border-radius: 0.25rem;
  font-size: 12px;
  line-height: 1;
  text-align: center;
  font-weight: 700;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  text-decoration: none;
}

.btn:hover, .btn:focus {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
}

.btn--load .icon {
  margin-right: 10px;
  line-height: 1;
  vertical-align: middle;
}

@media (max-width: 575px) {
  .btn {
    padding: 0.75rem 1rem;
  }
}

.btn-control {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 26px;
  height: 26px;
  padding: 0.25rem;
  border-radius: 0.25rem;
  color: #fff;
}

.btn-control .icon {
  font-size: 10px;
  line-height: 1;
  transform: rotate(0deg);
  transition: transform 120ms linear;
}

.btn-control.btn {
  border: none;
}

.nav-switch {
  border: 1px solid;
  padding: 3px;
  border-radius: 0.25rem;
  text-align: center;
}

.nav-switch__btn {
  background-color: transparent;
  font-size: 12px;
  padding: 6px 1.75rem;
  border: none;
  color: inherit;
  width: 50%;
  white-space: nowrap;
  cursor: pointer;
  outline: none !important;
  text-decoration: none;
}

.nav-switch__btn:after {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: currentColor;
  border-radius: 0.2rem;
  transform: translateX(100%);
  transition: transform 200ms;
}

.nav-switch__btn span {
  position: relative;
  transition: color 200ms;
  z-index: 2;
}

.nav-switch__btn:first-child {
  position: relative;
}

.nav-switch__btn:first-child:after {
  content: '';
}

.nav-switch__btn.active:after {
  transform: translateX(0);
}

.nav-switch__btn.active span {
  color: #fff;
}

