  display: inline-flex;
  flex-direction: column;
  text-align: left;
}

.page-footer__social .icon {
  width: 45px;
  text-align: center;
}

.account-avatar {
  max-width: 140px;
}

.account-avatar img {
  border-radius: 50%;
}

.account-nav__menu {
  border-bottom: none;
}

.account-nav__link {
  position: relative;
  display: block;
  width: 100%;
  color: #7D949A;
  padding: 0.5rem 0 0.5rem 1.75rem;
  border: none;
  border-left: 1px solid #B0CAD1;
  border-image: 0;
}

.account-nav__link::before {
  content: '';
  position: absolute;
  top: 5px;
  left: 0;
  height: calc(100% - 10px);
  border-left: 3px solid transparent;
}

.account-nav__link.active {
  color: #8FCE0D;
  background-color: transparent;
}

.account-nav__link.active::before {
  border-left-color: currentColor;
}

.payment-card {
  display: flex;
  margin: 0;
  padding: 0;
}

.payment-card .custom-control-input {
  height: 100%;
  width: 100%;
  z-index: 1;
  cursor: pointer;
}

.payment-card .custom-control-input:checked ~ .card, .payment-card .custom-control-input:checked ~ .franchise__content {
  border-color: #8FCE0D;
}

.payment-card .custom-control-input:invalid ~ .card, .payment-card .custom-control-input:invalid ~ .franchise__content {
  border-color: #EB5757;
}

.payment-card .custom-control-label {
  display: flex;
  flex-wrap: wrap;
  min-height: 140px;
  padding: 1.25rem;
  margin: 0;
}

.payment-card .custom-control-label:before, .payment-card .custom-control-label:after {
  left: auto;
  right: 1rem;
  top: 1.25rem;
  border-radius: 50%;
}

.payment-card__meta {
  width: 100%;
}

.payment-card__meta .date {
  font-size: 14px;
  letter-spacing: 0.04em;
}

.payment-card__meta .number {
  color: #7D949A;
  font-weight: 700;
  font-size: 1.25rem;
  letter-spacing: 0.05em;
  white-space: nowrap;
}

.franchise {
  padding-top: 2rem;
}

.franchise__row {
  padding: 1.9rem;
  padding-bottom: 0;
}

.franchise__row:last-of-type {
  padding-bottom: 1.9rem;
}

.franchise__row--divider {
  padding-bottom: 1.9rem;
  border-bottom: 1px solid rgba(176, 202, 209, 0.5);
}

.franchise__intro {
  margin-bottom: 0;
}

.franchise__intro-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 220px;
}

@media (min-width: 768px) {
  .franchise__intro-logo {
    height: 300px;
  }
}

.franchise__intro-footer {
  padding: 18px 30px 1rem;
  background-color: #082746;
  color: #fff;
}

.franchise__intro-footer-title {
  margin-bottom: 0.5rem;
  font-weight: 700;
  font-size: 18px;
  line-height: 1;
}

.franchise__intro-footer-description {
  margin-bottom: 0;
  font-size: 12px;
  line-height: 1.5em;
}

.franchise__thumbs-item {
  margin-bottom: 2rem;
}

.franchise__details {
  padding-top: 1.4rem;
  border-top: 1px solid rgba(176, 202, 209, 0.5);
}

.franchise__details .franchise-details {
  margin-bottom: 0;
}

.franchise__preview-video iframe {
  width: 100%;
  height: 174px;
}

@media (min-width: 560px) {
  .franchise__preview-video iframe {
    height: 230px;
  }
}

.franchise-details {
  padding: calc(1rem + 4px) 1.9rem;
  margin: 0 0 1.5rem;
  border-bottom: 1px solid rgba(176, 202, 209, 0.5);
}

.franchise-details__item {
  padding-top: 12px;
  padding-bottom: 12px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  text-align: center;
  flex-basis: 100%;
  max-width: 100%;
}

.franchise-details__item:not(:last-child) {
  border-bottom: 1px solid rgba(176, 202, 209, 0.5);
}

@media (min-width: 375px) {
  .franchise-details__item {
    flex-basis: 50%;
    max-width: 50%;
  }
  .franchise-details__item:not(:nth-child(2n)) {
    border-right: 1px solid rgba(176, 202, 209, 0.5);
  }
  .franchise-details__item:nth-last-of-type(2) {
    border-bottom: 0;
  }
}

@media (min-width: 768px) {
  .franchise-details__item {
    flex-direction: row;
    justify-content: center;
    flex-basis: 25%;
    max-width: 25%;
    padding-left: 0;
    padding-right: 0;
    text-align: left;
  }
  .franchise-details__item:not(:last-child) {
    border-bottom: 0;
    border-right: 1px solid rgba(176, 202, 209, 0.5);
  }
}

.franchise-details__icon {
  margin-bottom: 12px;
}

@media (min-width: 768px) {
  .franchise-details__icon {
    margin-bottom: 0;
    margin-right: 12px;
  }
}

.franchise-details__title {
  margin-bottom: 0.25rem;
  font-size: 12px;
  line-height: 1.16667em;
}

.franchise-details__text {
  white-space: nowrap;
  color: #082746;
}

.franchise-details__button {
  margin-top: calc(1rem + 4px);
}

.franchise-tab {
  padding: 1rem 1.5rem 0 calc(1rem + 4px);
  border-left: 4px solid transparent;
  background-color: #F3F6F7;
  opacity: 0.5;
  cursor: pointer;
  transition: opacity 200ms ease-in-out, border-color 200ms ease-in-out;
}

.franchise-tab--active {
  opacity: 1;
  border-left-color: #8FCE0D;
}

.franchise-tab__content {
  padding-bottom: 10px;
}

.franchise-tab:not(:last-child) .franchise-tab__content {
  border-bottom: 1px solid rgba(176, 202, 209, 0.5);
}

.franchise-tab__data {
  margin-bottom: 0.5rem;
  font-size: 12px;
  line-height: 1.16667em;
  color: #7D949A;
}

.franchise-tab__title {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 14px;
  line-height: 128%;
  color: #082746;
}

.franchise-tab__desc {
  font-size: 12px;
  line-height: 169%;
  max-height: 3.33333em;
  overflow: hidden;
}
